@import "../icon/iconfont.css";
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #f8f8f8;
}
body a {
  text-decoration: none;
}
body ul,
body ol {
  list-style: none;
}
body h1,
body h2,
body h3,
body h4,
body h5,
body h6,
body hr,
body p,
body blockquote,
body dl,
body dt,
body dd,
body pre,
body form,
body fieldset,
body legend,
body button,
body input,
body textarea,
body th,
body td,
body article,
body section,
body aside,
body div,
body header,
body footer,
body ul,
body ol,
body li {
  margin: 0;
  padding: 0;
}
body button {
  outline: none;
}
.btn.focus,
.btn:focus {
  outline: none;
  -webkit-box-shadow: none;
          box-shadow: none;
}
.close:focus,
.close:hover {
  outline: none;
}
img {
  max-width: 100%;
}
.container {
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}
.container-fluid {
  padding: 0;
}
.overflow-ellipsis {
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}
.cursor-pointer {
  cursor: pointer;
}



